export class Main extends Sprite {
	initializer:Initializer;

	private hospitalCanvas:HTMLCanvasElement;
	private gl:WebGLRenderingContext;
	private needToRender:boolean = true;  // draw at least once

	defaultWidth:number;
	defaultHeight:number;
	static ratio:number;
	width:number;
	height:number;


	constructor(media_path:string) {
		super();
		AssetLoader.MEDIA_PATH = media_path;
		console.log("MAIN constructor AssetLoader.MEDIA_PATH == "+AssetLoader.MEDIA_PATH);
		Logger.IS_DEV = true; // for logging to console

		this.init();
	}

	private init():void {
		Logger.log(this, "MAIN init");

		this.initializer = new Initializer();
		this.initializer.startGame("testGame");
	}

	public destroy():void {
		//TODO: need to flesh this out
		Logger.log(this, "Main destroy");
	}

}


( function($) {

	//Code block here
	$(window).on("load", function() {
		console.log("", "MAIN.ts load");
		$(document).trigger("startHospitalLocal", ["media/hospital/assets/"]); // TODO Add for local testing
	});

	$( document ).ready(function() {
		console.log( "ready!" );

	});

	$(document).on('startHospitalLocal', function (event:Event, media_path:string) {
		console.log("", "MAIN.ts startHospitalLocal media_path == "+media_path);
		new Main(media_path);
	});

	$(document).on('startHospital', function (event:Event, media_path:string) {
		console.log("", "MAIN.ts startHospital media_path == "+media_path);
		new Main(media_path);
	});


	$(document).on('deviceready', function (event:Event, media_path:string) {
		console.log("", "MAIN.ts deviceready");
		new Main(media_path);
	});



})(jQuery);



